@use '@styles/core/variables' as *;
@use '@styles/core/fonts' as *;
@use '@styles/tokens/colors' as *;

.noTech {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: $space-8;
  margin-top: 1.6rem;

  :global(.select-field) {
    margin-top: $space-8;
    width: auto;
    min-width: unset;
  }

  :global(.select__content) {
    font-size: $size-sm;
    display: grid;
    grid-template-columns: 1fr 20px;
  }

  :global(.select__caret) {
    display: block;
    &,
    & > svg {
      width: $space-20;
      height: $space-20;
    }
  }
}

.paragraph {
  text-align: center;
  padding: 0 1rem;
}
