@use '../../../styles/layout/breakpoints' as *;
@use '../../../styles/core/variables' as *;
@use '../../../styles/core/fonts' as *;
@use '../../../styles/tokens/colors' as *;

.dropdownMenu {
  width: 15.125rem;
}

.wrapper {
  display: none;
  align-items: center;
  justify-content: center;
  gap: 0 $space-16;
}

.content {
  display: flex;
  align-items: center;
  gap: 0 $space-16;
}

.separator {
  font-size: $size-base;
  font-style: normal;
  font-weight: $font-weight-regular;
  line-height: $size-xl;
  color: $on-surface-on-surface-40;
}

.item {
  display: flex;
  align-items: center;
  gap: 0 $space-8;
  cursor: pointer;
  height: 2.25rem;

  > span {
    display: block;
    font-size: $size-sm;
    font-style: normal;
    font-weight: $font-weight-regular;
    line-height: $size-lg;
    color: $on-surface-on-surface;
    letter-spacing: 0.00875rem;
  }

  > svg:first-child {
    width: $space-20;
    height: $space-20;
    fill: $on-surface-on-surface-40;
    color: $on-surface-on-surface-40;
  }

  > svg:last-child {
    width: 14px;
    height: 16px;
    fill: $accents-gray-900;
    color: $accents-gray-900;
  }
}

.wrapper.disabled {
  opacity: 0.5;
  pointer-events: none;
}

@media (min-width: $breakpoint-tablet) {
  .wrapper {
    display: flex;
  }
}
