@use '../../../styles/core/variables' as *;
@use '../../../styles/tokens/colors' as *;
@use '../../../styles/layout/breakpoints' as *;

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  max-height: 100vh;
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: $space-64;
  padding: 0 $space-16 0 $space-24;
  flex-shrink: 0;
}

.collapsed .header {
  padding-right: $space-12;
}

.toggleIcon {
  display: none; // Hidden by default (for desktop/tablet)
  align-items: center;
  justify-content: center;
}

.panelContent {
  flex: 1;
  display: flex;
  align-items: stretch;
  gap: $space-8;
  padding-bottom: $space-24;
  min-height: 0;

  display: grid;
  grid-template-columns: 4.875rem 1fr;

  :global(.side-panel__left) {
    width: auto;
    grid-area: unset;
  }
}

.navPanel {
  height: 100% !important;
  display: flex;
  flex-direction: column;
  overflow: auto !important;
  :global(.navbar) {
    overflow: auto !important;
  }

  :global(.navbar__top) {
    flex: 1;
    min-height: 0;

    display: flex;
    flex-direction: column;
  }

  :global(.navbar__items) {
    flex: 1;
    min-height: 0;
  }

  :global(.navbar__content) {
    // Critical: This is where Vision sidebar content is rendered
    // Must allow flex children to properly size and scroll
    flex: 1 1 0;
    min-height: 0;
    max-height: 100%;
    display: flex;
    flex-direction: column;
  }
}

.sidebarNav {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow-y: auto;
}

@media (max-width: $breakpoint-tablet) {
  .wrapper {
    position: fixed;
    inset: 0;
    z-index: 100;
    overflow-y: auto;
    padding: 0;
  }
  .header {
    position: sticky;
    top: 0;
    z-index: 10;
    backdrop-filter: blur(0.875rem);
    background: rgba($surface-surface-level-2, 0.6);
    padding: $space-16 0 $space-8;
    height: auto;
    margin: 0 #{-$space-16};
    padding-left: $space-16;
    padding-right: $space-16;
  }
  .panelContent {
    flex-direction: column;
  }
  .toggleIcon {
    display: flex; // Show toggle button on mobile
  }
}
