@use '@styles/core/variables' as *;
@use '@styles/core/fonts' as *;
@use '@styles/tokens/colors' as *;
@use '@styles/layout/breakpoints' as *;

.card {
  display: flex;
  flex-direction: column;
  gap: $space-8;
  padding: 0;
  border-radius: $space-16;
  overflow-y: auto;
  flex-wrap: wrap;
}

.accordion {
  :global(.accordion__item) {
    border-radius: 0.5rem;
  }
}

.loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: $space-16;
  padding: $space-32;
  min-height: 200px;
}

.noResults {
  display: flex;
  flex-direction: column;
  align-items: center;

  /* This remains important to break the long text */
  word-break: break-word;
}

.parametersTitle {
  grid-area: parameters-title;
  composes: title;
}

.parametersContent {
  grid-area: parameters-content;
  composes: content;
  flex-direction: column;
}

.parameter {
  display: flex;
}

.returnsTitle {
  grid-area: returns-title;
  composes: title;
}

.returnsContent {
  grid-area: returns-content;
  composes: content;
  flex-direction: column;
}

.exampleTitle {
  grid-area: example-title;
  composes: title;
}

.exampleContent {
  grid-area: example-content;
}

.icon {
  grid-area: icon;
  &:global(.icon) {
    height: 1rem;
    width: 1rem;
  }
}

.valueName {
  &:after {
    content: ':';
  }
}

.valueType {
  display: flex;
  padding: 0.125rem 0.25rem;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
  border-radius: $tag-radius;
  background: $surface-surface-level-0;

  color: $on-surface-on-surface;
  font-family: $font-family-base;
  font-size: $size-xs;
  font-style: normal;
  font-weight: $font-weight-regular;
  line-height: 0.875rem; /* 116.667% */
  letter-spacing: 0.0075rem;
  margin-right: auto;
}

.space {
  display: inline-flex;
  width: $space-4;
}

.breadcrumbs {
  :global(.breadcrumb) {
    user-select: none;
    cursor: pointer;
    border: none;
    color: $on-surface-on-surface-60;
    background-color: transparent;

    &:hover {
      background-color: transparent;
    }
  }

  :global(.breadcrumb-active) {
    color: $on-surface-on-surface !important;
  }
}

.noInstallation {
  margin-top: $space-16;
  color: $on-surface-on-surface-60;
  font-family: $font-family-base;
  font-size: $size-sm;
  font-style: normal;
  font-weight: $font-weight-regular;
  line-height: 1.125rem; /* 128.571% */
  letter-spacing: 0.00875rem;
}

.header {
  display: grid;
  gap: $space-24;
  margin-bottom: $space-8;
}

.accordion {
  :global(.accordion__item) {
    border-radius: $space-16;
    // Offset for sticky header: $space-56 (header height) + $space-16 (margin-top) = 72px
    // Add extra padding for better visibility on desktop
    scroll-margin-top: 100px; // Offset for sticky header + floating navigation on desktop

    @media (max-width: 767px) {
      scroll-margin-top: 80px; // Smaller offset on mobile
    }
  }

  :global(.accordion__content) {
    flex-wrap: wrap;
  }

  // Accordion header styling from Figma
  :global(.accordion__header) {
    gap: $space-16;
    border-bottom: 1px solid $utility-outline-mild;
  }

  // Title: 16px, medium weight (500), line-height 20px
  :global(.accordion__title),
  :global(.accordion__title-slot) {
    line-height: 20px !important;
    font-size: 16px;
    font-weight: 500;
  }

  // Description: 14px, regular weight, line-height 18px
  :global(.accordion__description) {
    font-family: $font-family-base;
    font-size: $size-sm; // 14px
    font-weight: $font-weight-regular;
    line-height: 1.125rem; // 18px
    letter-spacing: 0.00875rem; // 0.14px
    color: $on-surface-on-surface-60;
  }

  // Method type badge styling
  :global(.accordion__badge),
  :global(.accordion__tag) {
    height: 20px;
    border-radius: 4px;
    padding-top: 3px;
    padding-right: 6px;
    padding-bottom: 3px;
    padding-left: 6px;
    gap: 10px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-family: $font-family-base;
    font-size: $size-xs; // 12px
    font-weight: $font-weight-semibold; // 500
    line-height: 0.875rem; // 14px
    letter-spacing: 0.0075rem; // 0.12px
    color: $on-surface-on-surface;
    background-color: $state-state-primary-10;
  }

  // Code snippet content styling
  :global(.snippet__content) {
    min-height: 16px;
  }

  @media screen and (max-width: $breakpoint-desktop) {
    :global(.accordion__trigger) {
      padding: $space-12 !important;
    }
    :global(.accordion__content-inner) {
      padding: $space-12 !important;
    }
  }
}
