// Typography & Sizes
$size-5xl: 6rem;
$size-4xl: 2.5rem;
$size-3xl: 2rem;
$size-2xl: 1.5rem;
$size-xl: 1.375rem;
$size-lg: 1.125rem;
$size-base: 1rem;
$size-sm: 0.875rem;
$size-xs: 0.75rem;
$size-2xs: 0.625rem;

$mobile-5xl: 4rem;
$mobile-4xl: 2rem;
$mobile-3xl: 1.5rem;
$mobile-2xl: 1.375rem;
$mobile-xl: 1.25rem;

// Input
$input-large: 3rem;
$input-regular: 2.5rem;
$input-small: 2rem;
$input-x-small: 1.5rem;
$input-input-background: #ececec;
$input-text: #0d0c0d;
$input-text-dim: #0d0c0d;
$input-field-radius: 2rem;
$textarea-field-radius: 1rem;

// Radii
$card-radius: 1rem;
$section-radius: 1rem;
$checkbox-radius: 0.375rem;
$button-radius: 0.5rem;
$badge-radius: 1rem;
$tab-radius: 1rem;
$snackbar-radius: 1rem;
$avatar-radius: 0.5rem;
$tag-radius: 0.25rem;

// Buttons
$button-large: 3rem;
$button-regular: 2.5rem;
$button-small: 2rem;
$button-x-small: 1.5rem;
$button-xx-small: 1.25rem;

// Borders & Line Heights
$line-height-tight: 0.0625rem;
$line-height-regular: 0.0625rem;
$line-height-wide: 0.0625rem;
$border-regular: 0.0625rem;
$border-thick: 0.125rem;

// Spacing
$gap-cards: 1rem;
$gap-section-items-tight: 1rem;
$gap-section-items-regular: 2rem;
$gap-section-items-wide: 8rem;
$gap-page-sections: 16rem;
$gap-buttons: 0.5rem;

$padding-cards: 1rem;
$padding-section: 0rem;
$padding-page: 3.5rem;

$space-2: 0.125rem;
$space-3: 0.1875rem;
$space-4: 0.25rem;
$space-6: 0.375rem;
$space-8: 0.5rem;
$space-10: 0.625rem;
$space-12: 0.75rem;
$space-16: 1rem;
$space-20: 1.25rem;
$space-24: 1.5rem;
$space-32: 2rem;
$space-40: 2.5rem;
$space-48: 3rem;
$space-56: 3.5rem;
$space-64: 4rem;
$space-80: 5rem;
$space-96: 6rem;

$spacings: (
  2: $space-2,
  4: $space-4,
  8: $space-8,
  12: $space-12,
  16: $space-16,
  24: $space-24,
  32: $space-32,
  40: $space-40,
  48: $space-48,
  56: $space-56,
  64: $space-64,
  80: $space-80,
  90: $space-96,
);

$displays: grid, flex, block, inline, inline-grid, inline-flex, inline-block, none, contents;

$flex-directions: row, row-reverse, column, column-reverse;

$flex-wraps: nowrap, wrap, wrap-reverse;

$flex-flows: $flex-directions, $flex-wraps;

$aligns: normal, stretch, center, start, end, flex-start, flex-end, baseline;

$aligns-content: $aligns, space-between, space-around, space-evenly;

$aligns-items: $aligns, self-start, self-end, anchor-center;

$aligns-self: $aligns, self-start, self-end, anchor-center;

$justifies: center, start, end, flex-start, flex-end, left, right, normal, stretch;

$justifies-content: $justifies, space-between, space-around, space-evenly;

$justifies-items: $justifies, self-start, self-end, anchor-center, baseline;

$justifies-self: $justifies, self-start, self-end, anchor-center, baseline, auto;
