@use '@styles/core/variables' as *;
@use '@styles/tokens/colors' as *;

.overlay {
  display: flex;
  align-items: center;
  justify-content: center;

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  z-index: 1950;
  backdrop-filter: blur(4px);
  background-color: rgba(255, 255, 255, 0.8);
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;

  gap: $space-16;
  margin: 0;
  padding: 0;
}

.loader {
  width: 48px;
  height: 48px;
}

.spinner {
  width: 100%;
  height: 100%;
  border: 3px solid $utility-outline;
  border-top-color: $primary-primary;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.message {
  margin: 0;
  color: $on-surface-on-surface;
}
