@use '../../../styles/core/variables' as *;
@use '../../../styles/tokens/colors' as *;
@use '../../../styles/layout/breakpoints' as *;

// Default header variant
.wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: $space-16;
  margin: 0;
  padding: 0 $space-16;
  width: 100%;
  height: $space-56;
  border-radius: 4rem;
  background-color: $surface-surface-level-0;
}

.topbarLeft {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;

  > a.logo {
    display: block;
    margin: 0;
    padding: 0 0 0 $space-3;
    text-decoration: none;
  }

  .selector {
    display: none;
  }
}

.topbarRight {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;

  .searchbar {
    display: none;
    position: relative;
    width: 15rem;

    &.disabled {
      opacity: 0.5;
      pointer-events: none;
      filter: grayscale(0.3);
    }

    .comingSoonLabel {
      position: absolute;
      right: $space-8;
      top: 50%;
      transform: translateY(-50%);
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0;
      padding: $space-3 $space-6;
      font-size: $size-xs;
      line-height: $size-sm;
      color: $disabled-content-disabled-content;
      border-radius: $space-4;
      border: 1px solid $utility-outline-mild;
      background-color: $surface-surface-level-0;
      z-index: 1;
    }
  }

  .actions {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: $space-8;

    .search {
      display: block;

      &.disabled {
        opacity: 0.5;
        pointer-events: none;
        filter: grayscale(0.3);
      }
    }

    .notification {
      &.disabled {
        opacity: 0.5;
        pointer-events: none;
        filter: grayscale(0.3);
      }
    }

    .profile {
      display: none; // Hide on mobile

      .avatar {
        display: flex;
        cursor: pointer;

        // Prevent layout shift and smooth image transitions
        width: 32px;
        height: 32px;
        min-width: 32px;
        min-height: 32px;
        flex-shrink: 0;

        // Prevent any visual glitches during image loading
        will-change: contents;
        contain: layout style paint;

        :global(img) {
          transition: opacity 0.15s ease-in-out;
          image-rendering: -webkit-optimize-contrast;
          image-rendering: crisp-edges;
        }

        // Ensure fallback (initials) is always visible and stable
        :global([data-avatar-fallback]) {
          opacity: 1;
          transition: opacity 0.15s ease-in-out;
        }
      }
    }

    .navigation {
      display: block;
    }
  }
}

@media (min-width: $breakpoint-desktop) {
  .wrapper {
    padding: 0 $space-24;
    box-shadow: none;
    border-radius: 0;
    background-color: transparent;
  }

  .topbarLeft {
    > a.logo {
      display: none;
    }

    .selector {
      display: block;
    }
  }

  .topbarRight {
    gap: $space-16;

    .searchbar {
      display: block;
      position: relative;

      &.disabled {
        opacity: 0.5;
        pointer-events: none;
        filter: grayscale(0.3);
      }

      .comingSoonLabel {
        position: absolute;
        right: $space-8;
        top: 50%;
        transform: translateY(-50%);
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0;
        padding: $space-3 $space-6;
        font-size: $size-xs;
        line-height: $size-sm;
        color: $disabled-content-disabled-content;
        border-radius: $space-4;
        border: 1px solid $utility-outline-mild;
        background-color: $surface-surface-level-0;
        z-index: 1;
      }
    }

    .actions {
      gap: $space-16;

      .search {
        display: none;
      }

      .notification {
        &.disabled {
          opacity: 0.5;
          pointer-events: none;
          filter: grayscale(0.3);
        }
      }

      .profile {
        display: block;

        .avatar {
          // Prevent layout shift and smooth image transitions
          width: 32px;
          height: 32px;
          min-width: 32px;
          min-height: 32px;
          flex-shrink: 0;

          // Prevent any visual glitches during image loading
          will-change: contents;
          contain: layout style paint;

          :global(img) {
            transition: opacity 0.15s ease-in-out;
            image-rendering: -webkit-optimize-contrast;
            image-rendering: crisp-edges;
          }

          // Ensure fallback (initials) is always visible and stable
          :global([data-avatar-fallback]) {
            opacity: 1;
            transition: opacity 0.15s ease-in-out;
          }
        }
      }

      .navigation {
        display: none;
      }
    }
  }
}
