@use '../core/variables' as *;
@use '../tokens/colors' as *;
@use '../layout/breakpoints' as *;
@use '../../../../common/styles/core/mixins' as *;

.app-layout {
  display: grid;
  grid-template-columns: 1fr;
  width: 100%;
  height: 100vh;

  &.collapsed {
    grid-template-columns: 150px 1fr;

    // When layout is collapsed (mobile sidebar open), slide sidebar into view
    aside.sidebar {
      transform: translateX(0);
    }
  }

  aside.sidebar {
    @include sidebar-grid;
    // No transition for Vision as sidebar is not collapsable
    // transition: transform 250ms ease-in-out;
  }

  .main-content {
    @include main-content-base;
    height: 100%;

    header.topbar {
      @include topbar-base($z-index: 1050, $blur: 7px);
      justify-content: flex-start;
      padding: 0 $space-24;

      &:not(.unauthenticated) {
        position: sticky;
        top: 0;
      }

      &.unauthenticated {
        display: flex;
        height: $space-64;
      }
    }

    main.content {
      display: flex;
      flex-direction: column;
      flex: 1;
      margin: 0;
      padding: $space-24 $space-16 $space-32;
    }
  }
}

@media (min-width: $breakpoint-desktop) {
  .app-layout {
    grid-template-columns: 27.5rem 1fr;

    &.is-error {
      grid-template-columns: 10rem 1fr;
    }

    aside.sidebar {
      position: sticky;
      padding: 0;
      transform: translateX(0);
      height: 100vh;
    }

    .main-content {
      header.topbar {
        padding: 0;
      }

      main.content {
        display: flex;
        flex-direction: column;
        margin: 0;
        @include content-padding-desktop;
      }
    }
  }
}
