@use './variables' as *;
@use '../tokens/colors' as *;

// ===========================================
// Layout Mixins
// ===========================================

/// Base topbar styles - shared across all applications
/// @param {Number} $z-index [10] - z-index value
/// @param {Length} $blur [0.4375rem] - backdrop blur amount
@mixin topbar-base($z-index: 10, $blur: 0.4375rem) {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: $z-index;
  height: $space-56;
  backdrop-filter: blur($blur);
  background: rgba($surface-surface-level-2, 0.6);
  margin-top: $space-16;
  padding: 0 $space-16;
}

/// Base main-content styles - shared across all applications
@mixin main-content-base {
  flex: 1;
  display: flex;
  flex-direction: column;
}

/// Base sidebar styles for flexbox layout
@mixin sidebar-flexbox {
  flex-shrink: 0;
  background: $surface-surface-level-2;
  position: sticky;
  top: 0;
  height: auto;
  max-height: 100vh;
  box-sizing: border-box;
}

/// Base sidebar styles for grid layout (mobile-first with slide-in)
@mixin sidebar-grid {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  transform: translateX(-100%);
}

/// Content padding - desktop
@mixin content-padding-desktop {
  padding: 1.125rem $space-24 $space-24;
}

/// Content padding - mobile
@mixin content-padding-mobile {
  padding: $space-16;
}
