@use '@styles/core/variables' as *;
@use '@styles/core/fonts' as *;
@use '@styles/tokens/colors' as *;

.block {
  max-width: max-content;
}

.title {
  color: $on-surface-on-surface;
  font-family: $font-family-base;
  font-size: $size-sm;
  font-style: normal;
  font-weight: $font-weight-semibold;
  line-height: 1.125rem;
  letter-spacing: 0.00875rem;
  margin-bottom: $space-8;
}

.content {
  display: flex;
  padding: $space-8;
  border-radius: $space-8;
  background: $surface-surface-level-2;
  max-width: max-content;
}

.contentColumn {
  composes: content;
  flex-direction: column;
  align-items: flex-start;
}

.contentRow {
  composes: content;
  flex-wrap: wrap;
  align-items: center;
  word-break: break-word;

  color: $on-surface-on-surface-60;
  font-family: $font-family-base;
  font-size: $size-xs;
  font-style: normal;
  font-weight: $font-weight-regular;
  line-height: 0.875rem;
  letter-spacing: 0.0075rem;
}

.grid {
  display: flex;
  gap: $space-8;
  align-items: flex-start;
}

.column {
  display: flex;
  flex-direction: column;
  gap: $space-8;
}

.valueType {
  display: flex;
  padding: 2px 4px;
  justify-content: center;
  align-items: center;
  border-radius: $tag-radius;
  background: $surface-surface-level-0;

  color: $on-surface-on-surface;
  font-family: $font-family-base;
  font-size: $size-xs;
  font-style: normal;
  font-weight: $font-weight-regular;
  line-height: 0.875rem;
  letter-spacing: 0.0075rem;
}

.valueName {
  display: flex;
  align-items: center;
  height: 18px;

  color: $on-surface-on-surface-60;
  font-family: $font-family-base;
  font-size: $size-xs;
  font-style: normal;
  font-weight: $font-weight-regular;
  line-height: 0.875rem;
  letter-spacing: 0.0075rem;
}

.space {
  display: inline-flex;
  width: $space-4;
}

.parameterWrapper {
  display: flex;
  align-items: center;
}
