@use '@styles/core/variables' as *;
@use '@styles/core/fonts' as *;
@use '@styles/tokens/colors' as *;

.method {
  display: flex;
  flex-direction: column;
  gap: $space-24;
  align-items: flex-start;
  align-self: stretch;
  flex-wrap: wrap;
}

.parametersReturnsSection {
  display: flex;
  gap: $space-32;
  width: 100%;
  align-items: flex-start;
  position: relative;
  flex-wrap: wrap;
}

.returnsSection {
  max-width: max-content;
}

.exampleSection {
  width: 100%;
}

.exampleTitle {
  color: $on-surface-on-surface;
  font-family: $font-family-base;
  font-size: $size-sm;
  font-style: normal;
  font-weight: $font-weight-semibold;
  line-height: 1.125rem; /* 18px */
  letter-spacing: 0.00875rem; /* 0.14px */
  margin-bottom: $space-8;
}

.exampleContent {
  width: 100%;
}
