@use '@styles/core/variables' as *;
@use '@styles/tokens/colors' as *;

.container {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 100%;
  height: 100%;
  padding: $space-16 $space-24 $space-24;
  margin-top: 6.25rem;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  max-width: 560px;
  padding-bottom: $space-32;
}

.iconContainer {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 190px;
  height: 160px;
  background-color: $surface-surface-level-2;
  overflow: hidden;
}

.errorIcon {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
}

.title {
  margin: 0;
  font-size: $size-lg;
  font-weight: 500;
  line-height: 1.22;
  color: $on-surface-on-surface;
  text-align: center;
  margin-top: $space-16;
  margin-bottom: $space-16;
}

.message {
  margin: 0;
  font-size: $size-sm;
  font-weight: 400;
  line-height: 1.29;
  letter-spacing: 0.14px;
  color: $on-surface-on-surface;
  text-align: center;
  margin-bottom: $space-24;
}

.buttonContainer {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
