@use '../../../styles/core/variables' as *;
@use '../../../styles/tokens/colors' as *;
@use '../../../styles/layout/breakpoints' as *;

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
  animation: fadeIn 0.2s ease;

  @media (min-width: $breakpoint-desktop) {
    display: none;
  }
}

.drawer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  background-color: $surface-surface-level-2;
  z-index: 10000;
  transform: translateX(-100%);
  transition: transform 0.3s ease;
  overflow: hidden;

  &.open {
    transform: translateX(0);
  }

  @media (min-width: $breakpoint-desktop) {
    display: none;
  }
}

// Multi-view navigation container
.viewsContainer {
  display: flex;
  width: 300%; // 3 views
  height: 100%;
  transition: transform 0.3s ease;

  &.viewMain {
    transform: translateX(0);
  }

  &.viewProjects {
    transform: translateX(-33.333%);
  }

  &.viewWorkspaces {
    transform: translateX(-66.666%);
  }
}

.menuView {
  width: 33.333%;
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 $space-16 $space-8 $space-16;
  background-color: transparent;
  flex-shrink: 0;
  height: $space-56;
  margin-top: $space-16;
  transition: background-color 0.2s ease;

  &.headerScrolled {
    background-color: $constant-light;
  }
}

.headerLogo {
  display: flex;
  align-items: center;
}

.headerActions {
  display: flex;
  align-items: center;
  gap: $space-8;
}

.backButton {
  display: flex;
  align-items: center;
  gap: $space-8;
  background: none;
  border: none;
  cursor: pointer;
  padding: $space-8;
  margin-left: -$space-8;
  border-radius: $space-8;
  font-size: $size-base;
  font-weight: 500;
  color: $on-surface-on-surface;
  transition: background-color 0.2s ease;

  &:hover {
    background-color: $state-state-primary-10;
  }
}

.content {
  flex: 1;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  padding: $space-8 $space-16 $space-16;
  display: flex;
  flex-direction: column;
  gap: $space-16;
}

// Account & Project Section
.accountSection {
  display: flex;
  flex-direction: column;
  gap: $space-8;
  padding: $space-8 0;
  background-color: $surface-surface-level-0;
  border-radius: $card-radius;
}

.accountItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 $space-16;
  height: $space-40;
  background: none;
  border: none;
  cursor: pointer;
  width: 100%;
  transition: background-color 0.2s ease;

  &:hover {
    background-color: $state-state-primary-10;
  }
}

.accountItemContent {
  display: flex;
  align-items: center;
  gap: $space-16;
  flex: 1;
  min-width: 0;

  // Prevent avatar layout shift during image load
  // Neutral fallback background for Avatar
  > div:first-child {
    flex-shrink: 0;
    width: 32px;
    height: 32px;

    > span,
    > div {
      background-color: $surface-surface-level-3 !important;
    }
  }
}

.accountItemText {
  display: flex;
  flex-direction: column;
  gap: $space-2;
  min-width: 0;
  text-align: left;
}

.accountItemName {
  font-size: $size-sm;
  font-weight: 500;
  line-height: 1.286;
  letter-spacing: 0.01em;
  color: $on-surface-on-surface;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.accountItemEmail {
  font-size: $size-xs;
  font-weight: 400;
  line-height: 1.167;
  letter-spacing: 0.01em;
  color: $on-surface-on-surface-60;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.projectName {
  font-size: $size-sm;
  font-weight: 400;
  line-height: 1.286;
  letter-spacing: 0.01em;
  color: $on-surface-on-surface;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

// Expanded Lists (Projects / Workspaces)
.expandedList {
  display: flex;
  flex-direction: column;
  padding: $space-8 $space-16;
  gap: $space-4;
  border-top: 1px solid $utility-outline-mild;
  animation: slideDown 0.2s ease;
}

.expandedItem {
  display: flex;
  align-items: center;
  gap: $space-12;
  padding: $space-8 $space-12;
  height: $space-40;
  background: none;
  border: none;
  border-radius: $space-8;
  cursor: pointer;
  width: 100%;
  transition: background-color 0.2s ease;
  text-align: left;

  &:hover {
    background-color: $state-state-primary-10;
  }

  &.selected {
    background-color: $state-state-primary-20;
  }

  // Avatar in expanded item
  > div:first-child {
    flex-shrink: 0;
  }
}

.expandedItemLabel {
  flex: 1;
  font-size: $size-sm;
  font-weight: 400;
  line-height: 1.286;
  letter-spacing: 0.01em;
  color: $on-surface-on-surface;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.checkmark {
  color: $accents-pink-600;
  flex-shrink: 0;
}

.emptyMessage {
  padding: $space-12;
  font-size: $size-sm;
  color: $on-surface-on-surface-60;
  text-align: center;
}

// Submenu styles
.submenuSection {
  display: flex;
  flex-direction: column;
  gap: $space-8;
}

.submenuGroup {
  display: flex;
  flex-direction: column;
  background-color: $surface-surface-level-0;
  border-radius: $card-radius;
  padding: $space-8;
}

.submenuGroupLabel {
  font-size: $size-xs;
  font-weight: 500;
  line-height: 1.167;
  letter-spacing: 0.01em;
  color: $on-surface-on-surface-60;
  padding: $space-8 $space-12;
  text-transform: uppercase;
}

.submenuItem {
  display: flex;
  align-items: center;
  gap: $space-12;
  padding: $space-12 $space-12;
  height: $space-48;
  background: none;
  border: none;
  border-radius: $space-8;
  cursor: pointer;
  width: 100%;
  transition: background-color 0.2s ease;
  text-align: left;
  font-size: $size-sm;
  font-weight: 400;
  color: $on-surface-on-surface;

  &:hover:not(.disabled) {
    background-color: $state-state-primary-10;
  }

  &.selected {
    background-color: $state-state-primary-20;
  }

  &.disabled {
    cursor: not-allowed;
    opacity: 0.5;
  }

  span {
    flex: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.submenuSeparator {
  height: 1px;
  background-color: $utility-outline-mild;
  margin: $space-4 0;
}

.submenuItemIcon {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

// Navigation Section
.navigationSection {
  display: flex;
  flex-direction: column;
  flex: 1;
  min-height: 0; // Allow flex child to shrink and enable scrolling
  background-color: $surface-surface-level-0;
  border-radius: $card-radius;
  overflow: hidden;
}

.appHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0 $space-16;
  height: $space-56;
  background-color: $surface-surface-level-0;
  border: none;
  border-bottom: 1px solid $utility-outline-mild;
  border-top-left-radius: $card-radius;
  border-top-right-radius: $card-radius;
  cursor: pointer;
  transition: background-color 0.2s ease;

  &:hover {
    background-color: $state-state-primary-10;
  }

  &:active {
    background-color: $state-state-primary-20;
  }
}

.appHeaderContent {
  display: flex;
  align-items: center;
  gap: $space-8;
}

.appHeaderIcon {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;

  // Ensure icon SVG is visible (including GC icons with gradients)
  :global(.icon) {
    display: flex;
    width: 24px;
    height: 24px;
    // Reset icon-fill to allow gradient fallback for GC icons
    --icon-fill: initial;
  }

  :global(svg) {
    width: 24px;
    height: 24px;
  }
}

.appHeaderLabel {
  font-size: $size-base;
  font-weight: 500;
  line-height: 1.25;
  color: $on-surface-on-surface;
}

// All links container - shared padding for Add new + nav items
.allLinksContainer {
  display: flex;
  flex-direction: column;
  flex: 1;
  gap: $space-8;
  padding: $space-16;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.addNewButton {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: $space-8;
  width: 100%;
  height: $space-40;
  padding: 0 $space-16 0 $space-12;
  background-color: $primary-primary;
  color: $primary-on-primary;
  border: none;
  border-radius: 64px; // Pill shape
  font-size: $size-base;
  font-weight: 500;
  line-height: 20px;
  cursor: pointer;
  transition: background-color 0.2s ease;

  svg {
    flex-shrink: 0;
    fill: currentColor;
  }

  &:hover {
    background-color: $primary-primary-10;
  }

  &:active {
    background-color: $primary-primary-20;
  }
}

.navItems {
  display: flex;
  flex-direction: column;
}

.navGroup {
  display: flex;
  flex-direction: column;
}

.navGroupLabel {
  font-size: $size-xs;
  font-weight: 500;
  line-height: 1.167;
  letter-spacing: 0.01em;
  color: $on-surface-on-surface-60;
  padding: $space-8 $space-12;
  text-transform: uppercase;
}

.navItem {
  display: flex;
  align-items: center;
  gap: $space-12;
  padding: $space-8 $space-12;
  height: $space-40;
  background: none;
  border: none;
  border-radius: $space-8;
  cursor: pointer;
  width: 100%;
  transition: background-color 0.2s ease;
  text-align: left;

  &:hover:not(.disabled) {
    background-color: $state-state-primary-10;
  }

  &.active {
    background-color: $state-state-primary-20;
  }

  &.disabled {
    cursor: not-allowed;
    opacity: 0.5;
  }
}

.navItemIcon {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;

  // Ensure icon SVG is visible (including GC icons with gradients)
  :global(.icon) {
    display: flex;
    width: 24px;
    height: 24px;
    // Reset icon-fill to allow gradient fallback for GC icons
    // --icon-fill: initial;
  }

  :global(svg) {
    width: 24px;
    height: 24px;
  }
}

.navItemLabel {
  flex: 1;
  font-size: $size-sm;
  font-weight: 400;
  line-height: 1.286;
  letter-spacing: 0.01em;
  color: $on-surface-on-surface;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.navItemBadge {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: $space-16;
  height: $space-16;
  padding: 0 $space-4;
  font-size: $size-xs;
  font-weight: 500;
  line-height: 1.167;
  letter-spacing: 0.01em;
  color: $constant-light;
  background: $brand-gradient;
  border-radius: $badge-radius;
}

// Footer - Workspace Selector
.footer {
  padding: $space-16;
  flex-shrink: 0;
  background-color: $surface-surface-level-0;
  margin: 0 $space-16 $space-16;
  border-radius: $card-radius;
}

.workspaceSelector {
  display: flex;
  align-items: center;
  gap: $space-8;
  padding: $space-2 0;
  height: $space-40;
  background: none;
  border: none;
  cursor: pointer;
  width: 100%;
  transition: background-color 0.2s ease;
  border-radius: $space-8;

  &:hover {
    background-color: $state-state-primary-10;
  }

  // Prevent avatar layout shift during image load
  // Neutral fallback background for Avatar
  > div:first-child {
    flex-shrink: 0;
    width: 32px;
    height: 32px;

    > span,
    > div {
      background-color: $surface-surface-level-3 !important;
    }
  }
}

.workspaceSelectorText {
  display: flex;
  flex-direction: column;
  flex: 1;
  min-width: 0;
  text-align: left;
}

.workspaceSelectorName {
  font-size: $size-sm;
  font-weight: 500;
  line-height: 1.286;
  letter-spacing: 0.01em;
  color: $on-surface-on-surface;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.workspaceSelectorLabel {
  font-size: $size-xs;
  font-weight: 400;
  line-height: 1.167;
  letter-spacing: 0.01em;
  color: $on-surface-on-surface-60;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

// ==========================================
// App Picker Bottom Drawer
// ==========================================

.appPickerOverlay {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 10001;
  animation: fadeIn 0.2s ease;
}

.appPickerDrawer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: $surface-surface-level-0;
  border-top-left-radius: 24px;
  border-top-right-radius: 24px;
  z-index: 10002;
  overflow: hidden;
  animation: slideUp 0.3s ease;

  &:focus {
    outline: none;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0);
  }
}

.appPickerHeader {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: $space-64;
}

.appPickerDragHandle {
  width: 40px;
  height: 4px;
  background-color: $on-surface-on-surface-10;
  border-radius: 45px;
  margin-top: $space-8;
}

.appPickerTitle {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: $size-lg;
  font-weight: 500;
  line-height: 1.222;
  color: $on-surface-on-surface;
  text-align: center;
}

.appPickerDivider {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background-color: $utility-outline-mild;
}

.appPickerContent {
  display: flex;
  flex-direction: column;
  padding: $space-16;
  padding-bottom: $space-32; // Extra padding for safe area
}

.appPickerItem {
  display: flex;
  align-items: center;
  gap: $space-8;
  height: $space-48;
  padding: 0 $space-8;
  border: none;
  border-radius: $space-8;
  background: transparent;
  cursor: pointer;
  transition: background-color 0.2s ease;

  &:hover:not(.disabled) {
    background-color: $state-state-primary-10;
  }

  &.selected {
    background-color: $state-state-primary-10;
  }

  &.disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

.appPickerItemIcon {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;

  // Ensure icons render properly (including GC icons with gradients)
  :global(.icon) {
    display: flex;
    width: 24px;
    height: 24px;
    // Reset icon-fill to allow gradient fallback for GC icons
    --icon-fill: initial;
  }

  :global(svg) {
    width: 24px;
    height: 24px;
  }
}

.appPickerItemLabel {
  flex: 1;
  font-size: $size-base;
  font-weight: 400;
  line-height: 1.25;
  color: $on-surface-on-surface;
  text-align: left;
}

.appPickerCheckmark {
  flex-shrink: 0;
  color: $on-surface-on-surface;
}

.pickerEmptyMessage {
  padding: $space-16;
  font-size: $size-sm;
  color: $on-surface-on-surface-60;
  text-align: center;
}

.pickerSectionLabel {
  font-size: $size-xs;
  font-weight: 500;
  line-height: 1.167;
  letter-spacing: 0.01em;
  color: $on-surface-on-surface-60;
  padding: $space-8 $space-8;
  text-transform: uppercase;
}

// ==========================================
// Account Drawer
// ==========================================

.accountDrawer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: $surface-surface-level-0;
  border-top-left-radius: 24px;
  border-top-right-radius: 24px;
  z-index: 10002;
  overflow: hidden;
  animation: slideUp 0.3s ease;

  &:focus {
    outline: none;
  }
}

.accountDrawerHeader {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: $space-16;
}

.accountDrawerTitleSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: $space-12;
  padding: $space-16;
  padding-top: $space-24;
}

.accountDrawerTitleText {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: $space-4;
}

.accountDrawerName {
  font-size: $size-lg;
  font-weight: 500;
  line-height: 1.222;
  color: $on-surface-on-surface;
  text-align: center;
}

.accountDrawerEmail {
  font-size: $size-sm;
  font-weight: 400;
  line-height: 1.286;
  color: $on-surface-on-surface-60;
  text-align: center;
}

.accountDrawerContent {
  display: flex;
  flex-direction: column;
  padding: 0 $space-16 $space-32;
}

.accountDrawerItem {
  display: flex;
  align-items: center;
  gap: $space-12;
  height: $space-48;
  padding: 0 $space-12;
  border: none;
  border-radius: $space-8;
  background: transparent;
  cursor: pointer;
  transition: background-color 0.2s ease;

  &:hover:not(.disabled):not(.loading) {
    background-color: $state-state-primary-10;
  }

  &.disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &.loading {
    cursor: wait;
    opacity: 0.7;
  }
}

.accountDrawerItemIcon {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  color: $on-surface-on-surface;

  &.spinning {
    animation: spin 1s linear infinite;
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.accountDrawerItemLabel {
  flex: 1;
  font-size: $size-base;
  font-weight: 400;
  line-height: 1.25;
  color: $on-surface-on-surface;
  text-align: left;
}

.accountDrawerSeparator {
  height: 1px;
  background-color: $utility-outline-mild;
  margin: $space-8 0;
}

// ==========================================
// Add New Drawer
// ==========================================

.addNewDrawer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: $surface-surface-level-0;
  border-top-left-radius: 24px;
  border-top-right-radius: 24px;
  z-index: 10002;
  overflow: hidden;
  animation: slideUp 0.3s ease;

  &:focus {
    outline: none;
  }
}

.addNewDrawerContent {
  display: flex;
  flex-direction: column;
  padding: $space-16;
  padding-bottom: $space-32;
}

.addNewDrawerItem {
  display: flex;
  align-items: center;
  gap: $space-12;
  height: $space-48;
  padding: 0 $space-12;
  border: none;
  border-radius: $space-8;
  background: transparent;
  cursor: pointer;
  transition: background-color 0.2s ease;

  &:hover {
    background-color: $state-state-primary-10;
  }
}

.addNewDrawerItemIcon {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  color: $on-surface-on-surface;
}

.addNewDrawerItemLabel {
  flex: 1;
  font-size: $size-base;
  font-weight: 400;
  line-height: 1.25;
  color: $on-surface-on-surface;
  text-align: left;
}

// Vision Tree Styles
.visionTreeContainer {
  display: flex;
  flex-direction: column;
  gap: $space-12;
  // No padding here - parent allLinksContainer provides padding
}

.visionTreeSearch {
  width: 100%;
}

.visionTree {
  // Offset parent allLinksContainer padding to align with desktop
  margin: 0 (-$space-16);
  padding: 0 $space-8;

  // Match desktop Tree component styling
  :global(.tree) {
    gap: 0;
    padding: 0;
  }
}

.visionTreeNoResults {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: $space-8;
  padding: $space-32 $space-16;
  text-align: center;
}

.visionTreeNoResultsIcon {
  width: 32px;
  height: 32px;
  color: $on-surface-on-surface-40;
}

.visionTreeNoResultsTitle {
  font-size: $size-base;
  font-weight: 500;
  color: $on-surface-on-surface;
}

.visionTreeNoResultsMessage {
  font-size: $size-sm;
  color: $on-surface-on-surface-60;
}

// ==========================================
// Academy Tutorial Navigation Styles
// ==========================================

.academyTutorialContainer {
  display: flex;
  flex-direction: column;
  gap: $space-4;
  margin-bottom: $space-16;
}

.academyTutorialTree {
  display: flex;
  flex-direction: column;
  gap: $space-2;
}

.academyCategory {
  display: flex;
  flex-direction: column;
  margin-bottom: $space-2;
}

.academyCategoryHeader {
  display: flex;
  align-items: center;
  gap: $space-8;
  width: 100%;
  padding: $space-8 $space-12;
  border: none;
  background: transparent;
  cursor: pointer;
  text-align: left;
  transition: all 0.2s ease;
  border-radius: $button-radius;
  color: $on-surface-on-surface;
  font-size: $size-sm;
  font-weight: 500;

  &:hover {
    background-color: $surface-surface-level-1;
  }

  &:focus {
    outline: $border-regular solid $primary-primary;
    outline-offset: -1px;
  }

  &.hasActive {
    color: $primary-primary;
  }
}

.academyCategoryChevron {
  width: 1rem;
  height: 1rem;
  flex-shrink: 0;
  transition: transform 0.2s ease;
  color: $on-surface-on-surface-60;
  display: flex;
  align-items: center;
  justify-content: center;

  &.expanded {
    transform: rotate(90deg);
  }
}

.academyCategoryTitle {
  flex: 1;
  line-height: 1.4;
}

.academyCategoryContent {
  display: flex;
  flex-direction: column;
  margin-top: $space-2;
  padding-left: $space-16;
}

.academyArticle {
  display: flex;
  align-items: center;
  width: 100%;
  padding: $space-8 $space-12;
  border: none;
  background: transparent;
  cursor: pointer;
  text-align: left;
  transition: all 0.2s ease;
  border-radius: $button-radius;
  color: $on-surface-on-surface-80;
  font-size: $size-sm;

  &:hover {
    background-color: $surface-surface-level-1;
    color: $on-surface-on-surface;
  }

  &:focus {
    outline: $border-regular solid $primary-primary;
    outline-offset: -1px;
  }

  &.active {
    background-color: $surface-surface-level-2;
    color: $on-surface-on-surface;
    font-weight: 500;
  }
}

.academyArticleTitle {
  flex: 1;
  line-height: 1.4;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

// ==========================================
// Academy Main Navigation (Quick Start + Documentation)
// ==========================================

.academyMainNavigation {
  display: flex;
  flex-direction: column;
  gap: $space-4;
}

.academyBackToHome {
  display: flex;
  align-items: center;
  gap: $space-8;
  width: 100%;
  padding: $space-12 $space-16 $space-8;
  border: none;
  background: transparent;
  cursor: pointer;
  text-align: left;
  transition: all 0.2s ease;
  border-radius: $button-radius;
  color: $on-surface-on-surface-60;
  font-size: $size-sm;
  font-weight: 400;

  &:hover {
    background-color: $surface-surface-level-1;
    color: $on-surface-on-surface;
  }

  &:focus {
    outline: $border-regular solid $primary-primary;
    outline-offset: -1px;
  }
}

.academyBackToHomeIcon {
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  color: inherit;
}

.academyBackToHomeLabel {
  line-height: 1.4;
}

.academyMainNavItem {
  display: flex;
  align-items: center;
  gap: $space-12;
  width: 100%;
  padding: $space-12 $space-16;
  border: none;
  background: transparent;
  cursor: pointer;
  text-align: left;
  transition: all 0.2s ease;
  border-radius: $button-radius;
  color: $on-surface-on-surface;
  font-size: $size-base;
  font-weight: 400;

  &:hover {
    background-color: $surface-surface-level-1;
  }

  &:focus {
    outline: $border-regular solid $primary-primary;
    outline-offset: -1px;
  }

  &.active {
    background-color: $surface-surface-level-2;
    color: $primary-primary;
    font-weight: 500;
  }

  &.hasChildren {
    padding-right: $space-12;
  }
}

.academyMainNavIcon {
  width: 24px;
  height: 24px;
  flex-shrink: 0;
  color: inherit;
}

.academyMainNavLabel {
  flex: 1;
  line-height: 1.4;
}

.academyMainNavChevron {
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  transition: transform 0.2s ease;
  color: $on-surface-on-surface-60;

  &.expanded {
    transform: rotate(90deg);
  }
}

.academyDocumentationSection {
  display: flex;
  flex-direction: column;
}

.academyDocumentationChildren {
  display: flex;
  flex-direction: column;
  padding-left: $space-16;
  margin-top: $space-4;
}

.academySectionGroup {
  display: flex;
  flex-direction: column;
}

.academySectionHeader {
  display: flex;
  align-items: center;
  padding: $space-12 $space-16;
}

.academySectionHeaderLabel {
  font-size: $size-lg;
  font-weight: 500;
  color: $on-surface-on-surface;
  line-height: 1.4;
}

.academySectionChildren {
  display: flex;
  flex-direction: column;
  margin-top: $space-4;
}

// ==========================================
// Academy Quick Start Progress
// ==========================================

.academyQuickStartProgress {
  padding: $space-12 $space-16;
  margin-top: $space-12;
  background: $surface-surface-level-0;
  border-radius: $button-radius;
}

.academyQuickStartProgressHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: $space-8;
}

.academyQuickStartProgressLabel {
  font-size: $size-sm;
  font-weight: 400;
  color: $on-surface-on-surface-80;
}

.academyQuickStartProgressPercentage {
  font-size: $size-sm;
  font-weight: 500;
  color: $on-surface-on-surface;
}

.academyQuickStartProgressBar {
  width: 100%;
  height: 8px;
  background-color: $surface-surface-level-2;
  border-radius: $button-radius;
  overflow: hidden;
  margin-bottom: $space-8;
}

.academyQuickStartProgressBarFill {
  height: 100%;
  background-color: $primary-primary;
  border-radius: $button-radius;
  transition: width 0.3s ease;
}

.academyQuickStartProgressText {
  font-size: $size-xs;
  color: $on-surface-on-surface-60;
}

// ==========================================
// Academy Quick Start List (with checkboxes)
// Same styles as desktop TutorialMenu
// ==========================================

.academyQuickStartList {
  display: flex;
  flex-direction: column;
  gap: $space-4;
}

.academyQuickStartItem {
  display: flex;
  align-items: center;
  gap: $space-12;
  width: 100%;
  padding: $space-8 $space-12;
  border: none;
  background: transparent;
  cursor: pointer;
  text-align: left;
  transition: all 0.2s ease;
  border-radius: $button-radius;
  color: $on-surface-on-surface-80;
  font-size: $size-sm;

  &:hover {
    background-color: $surface-surface-level-1;
    color: $on-surface-on-surface;
  }

  &:focus {
    outline: $border-regular solid $primary-primary;
    outline-offset: -1px;
  }

  &.active {
    color: $on-surface-on-surface;
    font-weight: 500;
  }
}

.academyQuickStartCheckbox {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  padding: 0;
  border: none;
  background: transparent;
  cursor: pointer;
  flex-shrink: 0;
  transition: transform 0.2s ease;

  &:hover {
    transform: scale(1.1);
  }

  &:active {
    transform: scale(0.95);
  }
}

.academyQuickStartIconCircle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: $border-regular solid $utility-outline;
  background-color: $surface-surface-level-0;
  transition: all 0.2s ease;

  &.completed {
    border-color: $success-success;
    background-color: $success-success;
  }
}

.academyQuickStartCheckmark {
  width: 12px;
  height: 12px;
  color: $success-on-success;
  stroke: currentColor;
  fill: none;
}

.academyQuickStartLabel {
  flex: 1;
  line-height: 1.4;
}
