@use '@styles/core/variables' as *;

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;
  padding: $space-24;
  gap: $space-12;

  text-align: center;
}

.icon {
  font-size: 2rem;
  color: var(--color-text-secondary, #6b7280);
  opacity: 0.6;
}

.title {
  margin: 0;
  font-size: $size-sm;
  font-weight: 500;
  color: var(--color-text-primary, #1f2937);
}

.message {
  margin: 0;
  font-size: $size-xs;
  color: var(--color-text-secondary, #6b7280);
  max-width: 280px;
  line-height: 1.4;
}
