@use '../../../styles/core/variables' as *;
@use '../../../styles/tokens/colors' as *;
@use '../../../styles/layout/breakpoints' as *;

.wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: $space-16;
  width: 100%;

  @media (min-width: $breakpoint-tablet) {
    flex-wrap: nowrap;
  }
}

.leftSection {
  display: flex;
  flex: 1 0 0;
  flex-direction: column;
  gap: $space-4;
  min-width: 0;
}

.titleRow {
  display: flex;
  align-items: center;
  gap: $space-8;
  height: 32px;
}

.title {
  margin: 0;
  font-family: var(--primary-font, 'Outfit', sans-serif);
  font-size: $size-2xl;
  font-weight: 500;
  line-height: 1.25;
  color: $on-surface-on-surface;
}

.refreshButton {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: $space-8;
  width: 32px;
  height: 32px;
  border: none;
  border-radius: 100px;
  background: transparent;
  cursor: pointer;
  transition: background-color 0.2s ease;

  &:hover {
    background-color: $on-surface-on-surface-10;
  }

  &:active {
    background-color: $on-surface-on-surface-20;
  }
}

.refreshIcon {
  width: 20px;
  height: 20px;
  color: $on-surface-on-surface-60;
}

.description {
  margin: 0;
  font-family: var(--primary-font, 'Outfit', sans-serif);
  font-size: $size-sm;
  font-weight: 400;
  line-height: 1.29;
  letter-spacing: 0.14px;
  color: $on-surface-on-surface-60;
  white-space: pre-wrap;
}

.actionButton {
  font-weight: 500;
  width: 100%;

  @media (min-width: $breakpoint-tablet) {
    width: auto;
  }
}
