@use '../../../styles/core/variables' as *;
@use '../../../styles/tokens/colors' as *;
@use '../../../styles/layout/breakpoints' as *;

.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: auto auto 0 auto;
  box-sizing: border-box;
  max-width: 45rem;
}

.content {
  margin-top: $space-16;
  display: flex;
  align-items: center;
  gap: $space-24;
  width: 100%;
  padding: $space-8 $space-8 $space-8 $space-16;
  background-color: $surface-surface-level-0;
  border-radius: $space-16;
  box-sizing: border-box;
}

.message {
  flex: 1;
  margin: 0;
  font-size: $size-xs;
  font-weight: 500;
  line-height: $size-sm;
  letter-spacing: 0.14px;
  color: $on-surface-on-surface;

  @media (min-width: $breakpoint-tablet) {
    font-size: $size-sm;
    line-height: $size-lg;
  }
}
