@use '@styles/core/variables' as *;

.wrapper {
  display: flex;
  flex-direction: column;

  margin: 0;
  padding: 0 0 $space-24;

  width: 100%;
  height: 100%;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;

  margin: 0;
  padding: 0 $space-16;

  height: $space-64;
  flex-shrink: 0;

  > a.logo {
    display: block;

    margin: 0;
    padding: 0 0 0 $space-3;

    text-decoration: none;
  }
}

.sidebarNav {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;

  flex: 1;
}

.content {
  // Completely reworked scroll structure
  // This container takes full available height from navbar__content
  display: flex;
  flex-direction: column;

  width: 100%;
  height: 100%;

  // Critical: Use flex: 1 to take available space
  flex: 1 1 0;
  min-height: 0;
  max-height: 100%;

  gap: $space-8;
}

.search {
  // Search bar is fixed size, never shrinks
  flex: 0 0 auto;
  flex-shrink: 0;
  width: 100%;

  :global(input:focus) {
    border: 1px solid black;
  }
}

.tree {
  // Tree container is the ONLY scrolling element
  // It takes all remaining space and scrolls when content overflows
  display: flex;
  flex-direction: column;
  width: 100%;

  // Critical: flex: 1 takes remaining space, min-height: 0 allows shrinking
  flex: 1 1 0;
  min-height: 0;

  // This is where scrolling happens
  overflow-y: auto;
  overflow-x: hidden;

  // Custom scrollbar styling for better UX
  scrollbar-width: thin;
  scrollbar-color: rgba(0, 0, 0, 0.2) transparent;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 4px;

    &:hover {
      background-color: rgba(0, 0, 0, 0.3);
    }
  }
}

:global(.tree-item__label) {
  max-width: 15rem;
}

:global(.tree-item--active .tree-item__label) {
  font-weight: 600; // Bold for active item
}

:global(.tree) {
  overflow: auto !important;
}

.loading {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  flex: 1 1 0;
  min-height: 0;
  padding: $space-24;
}

.noResults {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  width: 100%;
  flex: 1 1 0;
  min-height: 0;
  padding: $space-24;
  gap: $space-12;

  text-align: center;
}

.noResultsIcon {
  font-size: 2rem;
  color: var(--color-text-secondary, #6b7280);
  opacity: 0.6;
}

.noResultsTitle {
  font-size: $size-sm;
  font-weight: 500;
  color: var(--color-text-primary, #1f2937);
}

.noResultsMessage {
  font-size: $size-xs;
  color: var(--color-text-secondary, #6b7280);
  max-width: 180px;
  line-height: 1.4;
}

.navbarBottomContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  width: 100%;
  gap: $space-16;
}
