.fieldCard {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.row {
  display: flex;
  align-items: center;
  gap: 8px;
}

.label {
  color: #6c7280;
  font-size: 14px;
}

.name {
  font-weight: 600;
  font-size: 16px;
}

.type {
  color: #6c7280;
  font-size: 14px;
}

.declaring {
  color: #6c7280;
  font-size: 13px;
}

.badge {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  padding: 2px 8px;
  border-radius: 12px;
  font-size: 12px;
  background: #eef2ff;
  color: #3949ab;
}

.badgeStatic {
  background: #fff4e5;
  color: #b76e00;
}

.exampleSection {
  margin-top: 12px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.exampleTitle {
  font-weight: 600;
  font-size: 14px;
}

.exampleContent {
  width: 100%;
}
