@use '@styles/core/variables' as *;
@use '@styles/core/fonts' as *;
@use '@styles/tokens/colors' as *;

.exampleFooter {
  background: $surface-surface-level-2;
  display: flex;
  flex-direction: column;
  gap: $space-8;
  align-self: stretch;
}

.exampleFooterWithLogs {
  padding-bottom: $space-12;
}

.exampleFooterHeader {
  display: flex;
  height: $space-40;
  padding: 0 $space-8 0 $space-4;
  align-items: center;
  gap: $space-8;
  align-self: stretch;
  border-bottom: 1px solid $surface-surface-level-0;
}

.log {
  font-size: $size-sm;
  padding: $space-4 $space-48 $space-4 $space-16;
  word-break: break-all;

  &::before {
    content: '>';
    margin-right: $space-8;
  }
}
