@use '@styles/core/variables' as *;
@use '@styles/core/fonts' as *;
@use '@styles/tokens/colors' as *;

.exampleHeader {
  display: flex;
  flex-direction: column;
}

.exampleHeaderButtons {
  display: grid;
  grid-template: 'primary reset . secondary' auto / auto auto 1fr auto;
  background: $surface-surface-level-2;
  padding: $space-8;
}

.tryItActive {
  grid-area: secondary;
}

.tryItInactive {
  grid-area: primary;
}

.run {
  grid-area: primary;
}

.reset {
  grid-area: reset;
  padding: 0.2rem 0.375rem !important;
}

.variables {
  display: flex;
  flex-direction: column;
  gap: $space-16;
  padding: $space-16 $space-16 0;
}

.variable {
  display: flex;
  align-items: center;

  :global(.input__wrapper) {
    width: auto;
  }
}
