@use '@styles/core/variables' as *;
@use '@styles/core/fonts' as *;
@use '@styles/tokens/colors' as *;

.wrapper {
  display: flex;
  align-items: center;
  gap: $space-8;
  position: relative;
  width: auto;
  height: 100%;
  user-select: none;
}

.icon {
  flex-shrink: 0;
  position: relative;
  width: 20px;
  height: 20px;
  overflow: clip;
  fill: rgba($on-surface-on-surface-60, 0.4) !important;
}

.name {
  position: relative;
  flex: 1;
  min-width: 0;
  font-family: $font-family-base;
  font-size: $size-sm;
  font-weight: 400;
  font-style: normal;
  line-height: 18px;
  letter-spacing: 0.14px;
  color: $on-surface-on-surface;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.copyButton {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  padding: 0;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}
