@use '@styles/core/variables' as *;
@use '@styles/layout/breakpoints' as *;

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 99;
  cursor: pointer;

  @media (min-width: $breakpoint-desktop) {
    display: none;
  }
}

.unauthenticated {
  height: $space-64;
  padding: 0 $space-24 !important;
}

.contentHeader {
  margin-bottom: $space-16;
}

.mobileMenuToggle {
  display: flex;
  margin-left: auto;

  @media (min-width: $breakpoint-desktop) {
    display: none;
  }
}
